@import url('https://fonts.googleapis.com/css?family=Spartan&display=swap');
@import url('https://fonts.googleapis.com/css?family=Oxanium&display=swap');
@import url('https://fonts.googleapis.com/css?family=Raleway&display=swap');
@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');
.about-block-wrapper {
    font-family: 'Roboto', sans-serif;
    height: 100%;
    display: flex;
    .main-top-block {
        margin-top: 20px;
        display: flex;
        justify-content: center;
        .image-appearance {
            margin-right: 15px;
            display: flex; 
            img {
                border: 1px solid black;
                border-radius: 999px;
            }
        }
        .about-block {
            h3 {
                margin: 0px;
                color: #707070;
                width: 100%;
            }
            p {
                color: #9e9e9e;
                font-weight: bold;
                size: 12px;
                width: 100%;
                text-align: left;
            }
            ul {
                padding: 0;
                display: inline-block;
                li {
                    margin: 0px 5px;
                    display: inline-block;
                    position: relative;
                    i {
                        margin: 0px;
                    }
                    &:first-child {
                        margin-left: 0px;
                        color: #45668e;
                    }
                    &:last-child {
                        color: #FF0000;
                    }
                }
            }
        }
    }
    .main-medium-block {
        margin: 20px 0px;
        display: flex;
        justify-content: center;
        width: 100%;
        .main-medium-content {
            border-radius: 5px;
            .main-medium-content-item {
                margin: 10px 0px;
                &:nth-child(2) {
                    label {
                        background-color: rgb(255, 136, 0);
                    }
                }

                p {
                   font-size: 15px;
                }

                a {
                    // color: black;
                    font-weight: bold;
                }
            }
        }
    }
}

.label-sm {
    font-family: 'Oxanium', 'sans-serif';
    display: inline;
    color: #000;
    margin: 0px;
    vertical-align: middle;
    font-size: 11px;
    font-weight: bold;
    background-color: rgb(251, 255, 0);
    padding: 3px 3px;
    border-radius: 3px;
    border: 1px solid rgb(141, 132, 0);
}

@media (min-width: 577px) {
    .image-appearance {
        img {
            width: 100px;
            height: 100px;
        }
    }
}

@media (max-width: 576px) {
    .main-top-block {
        justify-content: center;
    }

    .image-appearance {
        img {
            width: 100px;
            height: 100px;
        }
    }
    .about-block {

    }
    .main-medium-block {
        text-align: left;
    }
}